<template>
    <div class="scroller_cotainer" ref="scroller_cotainer">
        <slot></slot>
        <!-- 使用匿名插槽之后，nowplaying 中scroller 标签内部的 代码就会被显示在这里，这样的话就可以使该组件内部定义的better-scroll 可以起效果 -->
    </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
    name:"scroller",
    data: function(){
        return {
            pullDownMsg: "",
        }
    },
    mounted: function(){
        var _this = this;
         var scroll = new BScroll(this.$refs.scroller_cotainer, { // 自定义事件
            tap : true,
            probeType: 1, // 设置函数节流， 因为滑动的时候会频繁调用回调，所以这里设置其模式是节流的
        });

        //  将scroll 添加到该对象中作为一个属性
        this.scroll = scroll;

        scroll.on("scroll", function(pos){
            _this.handleToScroll(pos);
        });
        scroll.on("scroll", function(pos){
            _this.hendleToTouchEnd(pos);
        });
    },
    props: {
        // 接收父组件的函数
        handleToScroll: {
            type: Function,
            default: function(){},
        },
        hendleToTouchEnd:{
            type: Function,
            default: function(){},
        }
    },
    methods: {
        toScrollTop:function(y){
            this.scroll.scrollTo(0, y)
        }
    }
}
</script>
<style lang="scss" scoped>
    .scroller_cotainer{
        height: 100%;
    }
</style>
