const state = {
    nm: window.localStorage.getItem("nowNM") || "北京",
    id: window.localStorage.getItem("nowID") || 1,
}
const actions = {

}
const mutations = {
    CITY_CHANGE: function(state, payload){
        state.nm = payload.nm;
        state.id = payload.id;
    }
}

export default {
    namespaced: true,
    state,
    actions,
    mutations,
}