export default {
    path: "/cinema", 
    component: () => import("@/views/Cinema/cinema.vue"),
    children: [
        {
            path: "/",
            redirect: "wholecity"
            // 将已进入影院界面的路由重定向到全城
        },
        {
            path: "wholecity",
            component: () => import("@/components/WholeCity/wholecity.vue")
        },
        {
            path: "brand", 
            component: () => import("@/components/Brand/brand.vue")
        },
        {
            path: "detail",
            component: () => import("@/components/Detail/detail.vue")
        }
    ]
}